export const GET_METHOD = 'get';
export const POST_METHOD = 'post';
export const DELETE_METHOD = 'delete';

export default class Request {
    constructor(url, method = GET_METHOD, client) {
        this.url = url;
        this.method = method;
        this.client = client;
    }

    getUrl() {
        return this.url;
    }

    getMethod() {
        return this.method;
    }

    getClient() {
        return this.client;
    }
}
