const initialState = [];

const collection = (state = initialState, action) => {
    switch (action.type) {
        case 'SET_ITEM':
            return [
                ...action.items,
            ];
        case 'ADD_ITEM':
            return [
                ...state,
                action.item,
            ];
        case 'EDIT_ITEM':
            return state.map(item => {
                if (item.data[0].nasa_id === action.item.data[0].nasa_id) {
                    return action.item;
                }
                return item;
            });
        case 'DELETE_ITEM':
            return state.filter(item => item.data[0].nasa_id !== action.id);
        case 'TOGGLE_FAVORITE':
            return state.map(item => {
                if (item.data[0].nasa_id === action.id) {
                    item.isFavorite = !item.isFavorite;
                }
                return item;
            });
        case 'UNSET_FAVORITE':
            return state.map(item => {
                if (item.data[0].nasa_id === action.id) {
                    item.isFavorite = false;
                }
                return item;
            });
        default:
            return state;
    }
};

export default collection;
