import GetCollectionRequest from "src/services/Request/GetCollectionRequest";
import SaveCollectionRequest from "src/services/Request/SaveCollectionRequest";
import RequestFactory from "src/services/RequestFactory";

export const fetchCollection = () => {
    const factory = new RequestFactory();
    const request = new GetCollectionRequest();
    let result = factory.send(request);
    if (result) {
        result = JSON.parse(result);
    } else {
        result = [];
    }

    return {
        type: 'SET_ITEM',
        items: result,
    }
};

export const saveCollection = (data) => {
    const factory = new RequestFactory();
    const request = new SaveCollectionRequest();
    factory.send(request, JSON.stringify(data));
};
