import React, {Component, lazy, Suspense} from 'react';
import {BrowserRouter, Route, Switch} from "react-router-dom";
import AppLayout from "src/components/App/Layout/AppLayout";

const NasaCollectionList = lazy(() => import("./views/NasaCollection/List/NasaCollectionList"));
const NasaCollectionSearch = lazy(() => import("./views/NasaCollection/Search/NasaCollectionSearch"));

class App extends Component {
    render() {
        return (
            <AppLayout>
                <BrowserRouter basename={process.env.PUBLIC_URL}>
                    <Suspense fallback={<div>Loading...</div>}>
                        <Switch>
                            <Route path={'/'} exact component={NasaCollectionList}/>
                            <Route path={'/search'} exact component={NasaCollectionSearch}/>
                            <Route render={() => (<h1>PAGE NOT FOUND</h1>)}/>
                        </Switch>
                    </Suspense>
                </BrowserRouter>
            </AppLayout>
        );
    }
}

export default App;
