import {GET_METHOD, POST_METHOD, DELETE_METHOD} from "src/services/Request";

export default class LocalStorage {
    send(request, params = {}, method = GET_METHOD) {
        switch (method) {
            case GET_METHOD:
                return this.get(request, params);
            case POST_METHOD:
                return this.post(request, params);
            case DELETE_METHOD:
                return this.delete(request, params);
            default:
                return new Promise(() => {
                });
        }
    }

    /**
     *
     * @param request
     * @param params
     * @returns {*}
     */
    get(request, params = {}) {
        try {
            return window.sessionStorage.getItem(request);
        } catch (ex) {
            return null;
        }
    }

    post(request, params = {}) {
        try {
            window.sessionStorage.setItem(request, params);
        } catch (ex) {
            return null;
        }
    }

    delete(request) {
        try {
            window.sessionStorage.removeItem(request);
        } catch (ex) {
            return null;
        }
    }
}

